.ContactData {
  margin: 20px auto;
  width: 80%;
  text-align: center;
  box-shadow: 0 2px 3px 2px rgba(118, 112, 112, 0.952);
  border: 1px solid #eee;
  padding: 10px;
  box-sizing: border-box;
}
.ContactData form {
  display: inline-grid;
}

@media (min-width: 600px) {
  .ContactData {
    width: 500px;
  }
}
